"use client";

import { createContext, useContext, useEffect, useState, ReactNode } from "react";
const jwtDecode = require("jwt-decode");

interface Broker {
  _id: string;
  name: string;
  phoneNumber: string;
}

interface AuthContextType {
  broker: Broker | null;
  token: string | null;
  login: (token: string, broker: Broker) => void;
  logout: () => void;
  isAuthenticated: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [broker, setBroker] = useState<Broker | null>(null);
  const [token, setToken] = useState<string | null>(null);

  // On mount, load from localStorage
  useEffect(() => {
    const storedToken = localStorage.getItem("authToken");
    const storedBroker = localStorage.getItem("broker");
    if (storedToken && storedBroker) {
      setToken(storedToken);
      setBroker(JSON.parse(storedBroker));
    }
  }, []);

  const login = (token: string, broker: Broker) => {
    localStorage.setItem("authToken", token);
    localStorage.setItem("broker", JSON.stringify(broker));
    setToken(token);
    setBroker(broker);
  };

  const logout = () => {
    localStorage.removeItem("authToken");
    localStorage.removeItem("broker");
    setToken(null);
    setBroker(null);
  };

  return (
    <AuthContext.Provider
      value={{ broker, token, login, logout, isAuthenticated: !!token }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuthContext = () => {
  const context = useContext(AuthContext);
  if (!context) throw new Error("useAuthContext must be used within AuthProvider");
  return context;
};
